import '@babel/polyfill';
import axios from 'axios';
import {
    showAlert
} from './alert';

//type is eather 'contrasea' or 'datos'
export const updateSettings = async (data, type) => {
    try {
        const url =
            type === 'contrasea' ?
            '/api/v1/users/updateMyPassword/' :
            '/api/v1/users/updateMe';

        const res = await axios({
            method: 'PATCH',
            url,
            data,
        });

        console.log(res)

        if (res.data.status === 'success' && type === 'contrasea') {

            showAlert('success', 'Su contrasea se actualiz correctamente', 15);
            $('.center').hide();
            $('#show').show();

        } else if (res.data.status === 'success' && type === 'datos') {
            showAlert('success', 'Sus datos se actualizaron exitosamente', 15);
        }
    } catch (err) {
        if (err.response.data.message === "Invalid input data. Las contraseas no coinciden") {
            showAlert('error', err.response.data.message.split('.')[1], 5);
        } else {
            showAlert('error', err.response.data.message, 5);
        }

    }
};